<template>
    <div v-cloak id="root" :style="cssProps">
        <div class="card">
            <Transition name="fade" mode="out-in">
                <div v-if="step === 'start'">
                    <div><img v-if="image" class="image" :src="image" width="150" height="150" :alt="name" /></div>
                    <div>
                        <h3>{{ name }}</h3>
                        <p>{{ description }}</p>
                    </div>
                    <div>
                        <button type="button" class="button" @click="step = 'amount'">{{ buttonText }}</button>
                    </div>
                </div>

                <div v-else-if="step === 'amount'">
                    <div>
                        <h3>How many sats?</h3>
                    </div>
                    <div class="mb-1">
                        <input type="number" class="mb-1" name="amount" placeholder="Enter an amount" required
                            v-model.number="amount" />
                    </div>
                    <div>
                        <button type="button" class="button" @click="goToQR">Next</button>
                    </div>
                </div>

                <div v-else-if="step === 'qr'">
                    <h3>Scan to donate</h3>
                    <div class="mb-1">
                        <div class="qr-container" v-html="qrImage">
                        </div>
                    </div>
                    <button class="button" @click="step = 'thanks'">Done</button>
                </div>

                <div v-else-if="step === 'thanks'">
                    <div><img v-if="image" class="image" :src="image" width="150" height="150" :alt="name" /></div>
                    <div>
                        <h3>Thank you!</h3>
                        <p>🎉</p>
                    </div>
                    <div>
                        <button class="button" @click="reset">Start over</button>
                    </div>
                </div>
            </Transition>
        </div>
    </div>
</template>

<script>
import qrcode from 'qrcode-generator'


// function hexToRGB(color) {
//     const r = parseInt(color.slice(1, 3), 16)
//     const g = parseInt(color.slice(3, 5), 16)
//     const b = parseInt(color.slice(5, 7), 16)
//     return [r, g, b]
// }

// function luma(color) {
//     const rgb = hexToRGB(color)
//     return (0.299 * rgb[0] + 0.587 * rgb[1] + 0.114 * rgb[2]) / 255
// }

export default {
    name: "BitcoinWidget",
    props: {
        name: { type: String, required: true },
        description: { type: String, required: true },
        image: { type: String, required: true },
        address: { type: String, required: true },
        accent: { type: String, default: '#' },
        buttonText: { type: String, default: 'Donate bitcoin' },
        initialStep: { type: String, default: 'start'}
    },
    data() {
        return {
            step: this.initialStep,
            amount: this.initialStep === 'qr' ? '1000' : '',
            debugQRString: '',
            qrImage: ''
        }
    },
    computed: {
        cssProps() {
            return {
                '--accent': this.accent,
                '--color': '#FFFFFF', // Force white text for now
                '--button-color': this.accent,
            }
        }
    },
    mounted() {
        const fontImport = document.createElement("link");
        fontImport.setAttribute("href", "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");
        fontImport.setAttribute("rel", "stylesheet");
        fontImport.setAttribute("type", "text/css");
        document.head.appendChild(fontImport);

        if (this.initialStep === 'qr') {
            this.generateQR()
        }
    },
    methods: {
        generateQR() {
            const typeNumber = 4;
            const errorCorrectionLevel = 'L';
            const qr = qrcode(typeNumber, errorCorrectionLevel);

            const satsToBTC = this.amount ? (parseInt(this.amount) / 100000000).toFixed(8) : '';
            const qrString = `bitcoin:${this.address}?amount=${satsToBTC}`;

            qr.addData(qrString);
            qr.make();
            this.qrImage = qr.createImgTag(4); // pixel size
        },

        goToQR() {
            if (!this.amount || this.amount <= 0) return;
            this.generateQR();
            this.step = 'qr';
        },

        reset() {
            this.step = 'start';
            this.amount = '';
            this.qrImage = '';
        }
    },
}

</script>

<style>
abbr,
address,
article,
aside,
audio,
b,
blockquote,
body,
canvas,
caption,
cite,
code,
dd,
del,
details,
dfn,
div,
dl,
dt,
em,
fieldset,
figcaption,
figure,
footer,
form,
h1,
h2,
h3,
h4,
h5,
h6,
header,
hgroup,
html,
i,
iframe,
img,
ins,
kbd,
label,
legend,
li,
mark,
menu,
nav,
object,
ol,
p,
pre,
q,
samp,
section,
small,
span,
strong,
sub,
summary,
sup,
table,
tbody,
td,
tfoot,
th,
thead,
time,
tr,
ul,
var,
video {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent
}

body {
  line-height: 1
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block
}

nav ul {
  list-style: none
}

blockquote,
q {
  quotes: none
}

blockquote:after,
blockquote:before,
q:after,
q:before {
  content: "";
  content: none
}

a {
  margin: 0;
  padding: 0;
  font-size: 100%;
  vertical-align: baseline;
  background: transparent
}

ins {
  text-decoration: none
}

ins,
mark {
  background-color: #ff9;
  color: #000
}

mark {
  font-style: italic;
  font-weight: 700
}

del {
  text-decoration: line-through
}

abbr[title],
dfn[title] {
  border-bottom: 1px dotted;
  cursor: help
}

table {
  border-collapse: collapse;
  border-spacing: 0
}

hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: 1px solid #ccc;
  margin: 1em 0;
  padding: 0
}

input,
select {
  vertical-align: middle
}
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap');

* {
    font-family: "Inter", sans-serif !important;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.fade-enter-active,
.fade-leave-active {
    transition: all 0.2s ease-in-out;
}

.fade-enter-from,
.fade-leave-to {
    opacity: 0;
    transform: translateY(10px);
}

#root {
    position: relative;
    min-width: 250px;
    width: 100%;
}

.card {
    box-shadow:
        0px 2px 2px rgba(0, 0, 0, 0.06),
        0px 4px 4px rgba(0, 0, 0, 0.06),
        0px 8px 8px rgba(0, 0, 0, 0.06),
        0px 16px 16px rgba(0, 0, 0, 0.06),
        0px 32px 32px rgba(0, 0, 0, 0.06);
    border-radius: 25px;
    color: var(--color);
    padding: 30px 20px;
    background-color: var(--accent);
    height: 343px;
    width: 350px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.card>div {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}

.qr-container {
    background: #FFFFFF;
    padding: 12px;
    border-radius: 8px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06),
        0px 4px 4px rgba(0, 0, 0, 0.06),
        0px 8px 8px rgba(0, 0, 0, 0.06);
    width: 150px;
    height: 150px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.qr-container img {
    width: 100%;
    height: 100%;
    display: block;
}

.button {
    width: 150px;
    height: 40px;
    background: #FFFFFF;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06),
        0px 4px 4px rgba(0, 0, 0, 0.06);
    border-radius: 8px;
    border: 1px solid transparent;
    color: var(--button-color);
    font-weight: 700;
    font-size: 16px;
    transition: all 0.2s ease;
    cursor: pointer;
}

.button:hover {
    background: var(--accent);
    border: 1px solid #FFF;
    color: var(--color);
    transform: translateY(-1px);
}

.button:active {
    transform: translateY(1px);
}

.image {
    width: 130px;
    height: 130px;
    border-radius: 100px;
    object-fit: cover;
    background: #FFFFFF;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06),
        0px 4px 4px rgba(0, 0, 0, 0.06),
        0px 8px 8px rgba(0, 0, 0, 0.06);
}

input {
    width: 100%;
    height: 40px;
    border: none;
    border-radius: 8px;
    padding: 10px 20px;
    text-align: center;
    color: var(--button-color);
    font-weight: 700;
    font-size: 16px;
    outline: none;
    background: #FFFFFF;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.06),
        0px 4px 4px rgba(0, 0, 0, 0.06);
}

input::placeholder {
    color: var(--button-color);
    opacity: 0.5;
}

input[type="number"] {
    -moz-appearance: textfield;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
}

h3 {
    font-size: 20px;
    font-weight: 700;
    color: var(--color);
    margin-bottom: 8px;
}

p {
    font-size: 16px;
    color: var(--color);
    line-height: 1.5;
}
</style>